import React from 'react'
import { useDispatch } from 'react-redux'
import styled from 'styled-components'
import { enterRoom } from '../features/appSlice';
import {db} from './firebase'



export default function SidebarOption({Icon, title, addChannelOption, id}) {
    const dispatch = useDispatch();

    const addChannel = () => {
        const channelName = prompt('Please enter the channel name')
        if(channelName) {
            db.collection('rooms').add({
                name: channelName
            })
        }
    }

    const selectChannel = () => {
        if (id) {
            dispatch(enterRoom({
                roomID: id
            }))
        }
    }



    return (
        <SidebarOpContainer onClick={addChannelOption ? addChannel : selectChannel}>
            {Icon && <Icon fontSize='small' style={{padding: 10}}/>}
            {Icon ? (
                <H3>{title}</H3>
            ) : (
                <SidebarOpChannel>
                    <Span>#</Span> {title}
                </SidebarOpChannel>
            )}
        </SidebarOpContainer>
    )
}

const SidebarOpContainer = styled.section`
    display: flex;
    font-size: 0.8rem;
    align-items: center;
    padding-left: 2px;
    cursor: pointer;
    &:hover {
        opacity: 0.9;
        background-color: #340e36;
    }
`
const H3 = styled.h3`
    font-weight: 500;
`
const Span = styled.span`
    padding: 15px;
`
const SidebarOpChannel = styled.h3`
    padding: 3px 0;
    font-weight: 300;
`