import React from 'react'
import styled from 'styled-components'



export default function Msg({message, timestamp, user, userImage}) {
    return (
        <MsgContainer>
            <Img src={userImage} referrerpolicy="no-referrer"/>
            <Info>
                <H4>
                    {user}{' '}
                    <Span>{new Date(timestamp?.seconds * 1000).toUTCString()}</Span>
                </H4>
                <P>{message}</P>
            </Info>
        </MsgContainer>
    )
}

const MsgContainer = styled.div`
    display: flex;
    align-items: center;
    padding: 20px;
`
const Img = styled.img`
    height: 50px;
    border-radius: 8px;
`
const Info = styled.div`
    padding-left: 10px;
`
const H4 = styled.h4`

`
const Span = styled.span`
    color: grey;
    font-weight: 300;
    margin-left: 4px;
    font-size: 0.7rem;
`
const P = styled.p`

`